import { Messengers } from "../helpers/Messengers";
import {
    TimedMetadata,
    PlayerEvent,
    SessionLive,
    SessionProperties,
    // YoLog,
    // DebugFlags,
} from "@yospace/admanagement-sdk";
import { parseYospaceID3Tag } from "../helpers/yospace-tags";
import { PlayerStates } from "../helpers/utils";

// YoLog.setDebugFlags(DebugFlags.DEBUG_VALIDATION);

class LiveAAMPYospace {
    currentTime = -1;
    listeners = [];
    futureTags = [];
    currentAdId = null;
    playerState = PlayerStates.Initialising;
    hasPlayed = false;

    session = null;

    hasProcessedFirstTag = false;

    adBreakContentBeginTimeout = null;

    init = () => {
        this.removeListeners(); // remove in case monitoringTeardown isn't called
        this.addListeners();
    };

    createSession = async (playbackUrl) => {
        if (this.session) {
            return;
        }
        this.session = await SessionLive.create(
            playbackUrl,
            new SessionProperties()
        );
        return this.session?.getPlaybackUrl();
    };

    addListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.id3Metadata$.addReceiver(this.onID3TagReceived),
            Messengers.timeUpdate$.addReceiver(this.onTimeUpdate),
            Messengers.playerState$.addReceiver(this.onPlayerStateChange, true),
            Messengers.appTeardown$.addReceiver(this.onAppTeardown),
        ];
    };

    removeListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onAppTeardown = () => {
        this.removeListeners();
        if (this.session) {
            this.session?.shutdown();
            this.session = undefined;
        }

        this.currentTime = -1;
        this.listeners = [];
        this.futureTags = [];
        this.currentAdId = null;
        this.hasProcessedFirstTag = false;
        this.playerState = PlayerStates.Initialising;
        this.hasPlayed = false;
    };

    onID3TagReceived = (aampId3Data) => {
        if (!aampId3Data.detail?.presentationTime) return;

        const { presentationTime, timeScale, data } = aampId3Data.detail;
        const id3TagTime = presentationTime / timeScale;

        const yospaceTagOrNull = parseYospaceID3Tag(data);
        this.futureTags.push({
            time: id3TagTime,
            tag: yospaceTagOrNull,
        });

        // case where we enter the stream during an ad and get id3 events first. We assume if the
        // first id3 tag we get is an ad tag we are starting content in an ad break.
        // Otherwise analytics will get content progress events before we pass the ID3 start time
        // in futureTags below
        if (
            this.currentTime === -1 &&
            yospaceTagOrNull &&
            !this.currentAdId &&
            !this.hasProcessedFirstTag
        ) {
            const firstTag = this.futureTags[0]?.tag;
            this.currentAdId = firstTag?.adData?.adId;
            console.log(
                "[acc] !! got yospace tag before first time update, sending as ad start event"
            );
            Messengers.currentAd$.send(this.currentAdId);
        }
        this.hasProcessedFirstTag = true;
    };

    adIdsAreTheSame = (adId1, adId2) => {
        return (!adId1 && !adId2) || adId1 === adId2;
    };

    onTimeUpdate = (currentTime) => {
        if (currentTime < this.currentTime) {
            console.warn(
                `[acc] Went ${
                    this.currentTime - currentTime
                } seconds back in time, this should not happen`
            );
            // arbitrary 5, if we seek back or reset for whatever reason, clear ad tags as who knows what's going on
            if (this.currentTime - currentTime > 5) {
                console.warn("[acc] clearing ad tags...");
                this.futureTags = [];
            }
        }
        this.currentTime = currentTime;

        while (this.futureTags[0]?.time < this.currentTime) {
            // ad logic
            const nextTag = this.futureTags.shift();
            const nextAdId = nextTag.tag?.adData?.adId;
            if (!this.adIdsAreTheSame(nextAdId, this.currentAdId)) {
                Messengers.currentAd$.send(nextAdId || null);
            }

            // yospace tag logic
            const yoAttributes = nextTag?.tag?.attributes;
            if (yoAttributes) {
                const metadata = TimedMetadata.createFromMetadata(
                    yoAttributes.YMID,
                    yoAttributes.YSEQ,
                    yoAttributes.YTYP,
                    Number(yoAttributes.YDUR),
                    this.currentTime * 1000
                );
                this.session?.onTimedMetadata(metadata);
            }

            // ad break end logic
            const adData = nextTag.tag?.adData;
            if (adData) {
                const isLastTagInAd =
                    adData.currentFragNumberInAd === adData.totalFragsInAd &&
                    adData.tagPositionInFrag === "E";

                if (isLastTagInAd) {
                    this.adBreakContentBeginTimeout = window.setTimeout(() => {
                        // If there is another ad, we expect its starting id3 tag pretty
                        // much immediately after the previous ad's ending id3 tag.
                        // This timeout should be cleared when any new yospace tag is
                        // emitted (below)
                        // Arbitrary 1 seconds for the timer should be enough time +
                        // get an ad break ended event within a second accuracy
                        Messengers.currentAd$.send(null);
                    }, 1000);
                } else if (this.adBreakContentBeginTimeout) {
                    clearTimeout(this.adBreakContentBeginTimeout);
                    this.adBreakContentBeginTimeout = null;
                }
            }

            this.currentAdId = nextAdId || null;
        }
    };

    onPlayerStateChange = (playerState) => {
        const prevState = this.playerState;
        this.playerState = playerState;

        if (this.playerState === PlayerStates.Playing) {
            if (!this.hasPlayed) {
                this.session?.onPlayerEvent(
                    PlayerEvent.START,
                    this.currentTime
                );
                this.hasPlayed = true;
            } else {
                if (prevState === PlayerStates.Buffering) {
                    this.session?.onPlayerEvent(
                        PlayerEvent.CONTINUE,
                        this.currentTime
                    );
                } else {
                    this.session?.onPlayerEvent(
                        PlayerEvent.RESUME,
                        this.currentTime
                    );
                }
            }
        } else if (this.playerState === PlayerStates.Buffering) {
            if (prevState === PlayerStates.Playing) {
                this.session?.onPlayerEvent(
                    PlayerEvent.STALL,
                    this.currentTime
                );
            }
        }
    };
}

export default new LiveAAMPYospace();
