import youbora from "youboralib";
import { Store } from "../helpers/nine-store";
import { Messengers } from "../helpers/Messengers";
import { customAAMPAdapter } from "./custom-npaw-aamp-adapter";
import { getActiveListing } from "../nine-services/nine-program-tracker";

const AppVersion = Store.appVersion;

class NpawClient {
    plugin;
    listeners = [];
    contentCdn;

    init = () => {
        this.removeListeners(); // remove in case monitoringTeardown isn't called

        const useProd = Store.useProdConfig;
        const accountCode = useProd ? "nine" : "ninedev";
        const config = {
            accountCode,
            "background.enabled": true,
            "app.name": "ctv_foxtel_mle", // TODO
            "app.releaseVersion": AppVersion,
        };
        youbora.adapters.AAMP = customAAMPAdapter;
        this.plugin = new youbora.Plugin(config);

        // youbora.Log.logLevel = youbora.Log.Level.DEBUG;
        console.log("[acc] NPAW: configured", config);

        this.addListeners();
    };

    addListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.streamReady$.addReceiver(
                this.onChannelDataReceived,
                true
            ),
            Messengers.programChange$.addReceiver(this.updateNpawProperties),
            Messengers.appTeardown$.addReceiver(this.onAppTeardown),
            Messengers.playerError$.addReceiver(this.onPlayerError),
        ];
    };

    removeListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onAppTeardown = () => {
        this.onPlayerDestroy();
        this.removeListeners();
        this.plugin = null;
    };

    /**
     * Update plugin with adapter to use for the currently playing video.
     */
    onPlayerInit = () => {
        this.plugin.enable();
        const adapter = new youbora.adapters.AAMP();
        adapter.setIsLive(true);
        this.plugin.setAdapter(adapter);

        // do play event as soon as we have channel
        // data and an adapter ready
        adapter.playListener();
    };

    /**
     * Remove player adapter when playback has ended.
     */
    onPlayerDestroy = () => {
        this.plugin.removeAdapter();
        this.plugin.disable();
    };

    updateNpawProperties = (channelData) => {
        if (!channelData) return;

        if (!this.contentCdn) {
            const streamUrl = channelData.stream?.url || "";
            const urlQuery = new URLSearchParams(streamUrl);

            // yo.ap or yo.up value reveals underlying cdn
            // akamai value https://p-9now-ys-v2.akamaized.net/
            // cloudfront value https://livestream-cdn.9vms.com.au/
            let cdnValue = "";
            const yoApValue = urlQuery.get("yo.ap");
            const yoUpValue = urlQuery.get("yo.up");

            const akamaiString = "akamaized.net";
            const cloudfrontString = "livestream-cdn.9vms.com.au";

            if (
                yoApValue?.includes(akamaiString) ||
                yoApValue === "https" ||
                yoUpValue?.includes(akamaiString) ||
                yoUpValue === "https"
            ) {
                // special 'https' value means akamai for legacy uplift
                cdnValue = "AKAMAI";
            } else if (
                yoApValue?.includes(cloudfrontString) ||
                yoUpValue?.includes(cloudfrontString)
            ) {
                cdnValue = "CLOUDFRT";

                // if no yospace parameters check url for CDN
            } else if (streamUrl.includes(akamaiString)) {
                cdnValue = "AKAMAI";
            } else if (streamUrl.includes(cloudfrontString)) {
                cdnValue = "CLOUDFRT";
            }

            // fallback to yoApValue because for debugging in case unexpected values
            // come through
            this.contentCdn = {
                "content.cdn": cdnValue || yoApValue || "<unknown>",
            };
        }

        const program = getActiveListing(channelData);
        const nuid = Store.nuid;
        const platformString =
            Store.devicePlatform.toLowerCase() === "soip-pck" ? "PUCK" : "TV";

        this.plugin.setOptions({
            "content.isLive": true,
            "content.playbackType": "live",
            "content.title": program?.name, // if events do "event.name - event.subtitle"
            "content.channel": channelData.referenceId ?? "", // for events do event.name
            "content.id": channelData.referenceId ?? "",
            "content.type": "channel",

            ...this.contentCdn,

            // If there are less than 20 custom dimensions
            // we should pass each as a dimension with a magic number on
            // it

            "content.customDimension.1":
                Messengers.oztamSessionId$.getCurrentValue(),
            "content.customDimension.6": Store.getOztamVendorVersion(),
            "content.customDimension.5": "live_page", // matches oztam usr1
            "content.customDimension.2": String(false), // start over
            "content.customDimension.3": String(!!channelData.ssai), // monetised

            "user.name": nuid ?? "",
            "user.type": nuid ? "registered" : "unregistered",

            // hardcode to get around npaw library bug
            "content.streamingProtocol": "HLS",

            "device.code": `HUBBL_${platformString}`,
            "device.type": platformString === "TV" ? "TV" : "STB",

            // will show as Device Vendor on back-end
            "device.brand": "Foxtel",
        });
    };

    /**
     * Update plugin with data of the currently playing video.
     */
    onChannelDataReceived = (channelData) => {
        if (!channelData) return;

        // reset in case cdn changes across channels
        this.contentCdn = null;

        this.updateNpawProperties(channelData);
        this.onPlayerInit();
    };

    /**
     * Emit player error events manually. Do not add error listeners in npaw
     * adapter because aamp only emits fatal errors to us.
     */
    onPlayerError = (errorData) => {
        const errorMessage = errorData?.detail?.description || "PLAY FAILURE"; // default from Npaw
        const errorCode = errorData?.detail?.code || "<unknown>";

        // assume error is always fatal
        this.plugin.fireFatalError(errorCode, errorMessage);
        this.onPlayerDestroy();
    };
}

export default new NpawClient();
