// analytics libraries
import "./lib/adobe-analytics-libraries";

import { MessengerLink } from "./helpers/MessengerLink";
import { Messengers } from "./helpers/Messengers";
import { Store } from "./helpers/nine-store";
import { NetworkConnectionType } from "./helpers/utils";
import { ErrorTypes, MLEError } from "../provider-utils";
import {
    getAndStoreConfig,
    get9NowPlaybackData,
    getApiRegion,
} from "./nine-services/nine-api";
import { refreshAuth } from "./nine-services/nine-auth";

import AdobeAnalytics from "./analytics/AdobeAnalytics";
import AdobeAudienceManager from "./analytics/AdobeAudienceManager";
import OztamBridge from "./analytics/OztamBridge";
import LiveAAMPYospace from "./analytics/YospaceAds";
import Npaw from "./analytics/Npaw";

import "./nine-services/nine-program-tracker";

/**
 * @typedef {Object} getAssetObject
 * @property {Array.streamObject} streams list of streams to attempt to play
 */

/**
 * @typedef {Object} streamObject
 * @property {string} url manifest/stream url
 * @property {object} drmConfig see AAMP.setDrmConfig() https://code.rdkcentral.com/r/plugins/gitiles/rdk/components/generic/aamp/+/refs/heads/stable2/AAMP-UVE-API.md#setdrmconfig_config
 * @property {Array.requestHeaderObject} requestHeaders see AAMP.addCustomHTTPHeader() https://code.rdkcentral.com/r/plugins/gitiles/rdk/components/generic/aamp/+/refs/heads/stable2/AAMP-UVE-API.md#addcustomhttpheader_headername_headervalue_islicenserequest
 */

/**
 * @callback requestHeaderObject
 * @param {string} key header name
 * @param {string} value header value
 * @param {boolean} isLicenseRequest is for license requests only
 */
export default class NineProvider {
    config;
    messengerLink;

    constructor(config) {
        this.config = config;
    }

    /**
     * @typedef {import('../../app/get-asset').GetAssetRequest} GetAssetRequest
     * @typedef {import('../../app/get-asset').GetAssetResponse} GetAssetResponse
     */
    /**
     * @param {GetAssetRequest} args requested asset and device information
     * @returns {GetAssetResponse} streams for the requested asset
     */
    async getAsset({
        authToken,
        glassAssetLocator,
        glassContentOptions,
        deviceInfo,
    }) {
        let gracenoteId = getFileName(glassAssetLocator.pathname);
        const provider = glassAssetLocator.searchParams.get("provider");
        const providerId = glassAssetLocator.searchParams.get("providerid");

        const {
            glassConfigure: { brand: devicePlatform, version: deviceOSVersion },
            uuid: deviceId,
        } = deviceInfo;
        const { connectionType: networkConnectionType } = glassContentOptions;

        let nineNowAuthData;
        try {
            nineNowAuthData = JSON.parse(atob(authToken));
        } catch (e) {
            throw new MLEError(
                "Could not parse 9now auth data",
                ErrorTypes.GENERIC
            );
        }

        console.info("[acc] Nine received startup data", {
            nineNowAuthData,
            glassAssetLocator,
            glassAssetLocatorParsed: {
                gracenoteId,
                provider,
                providerId,
            },
            glassContentOptions,
            deviceInfo,
            nineNowAuthData,
        });

        console.info(
            `[acc] Accedo version ${Store.appVersion}, parent app version ${deviceInfo.appVersion}, player version ${deviceInfo.playerVersion}`
        );

        let channelSlug, regionFromParent;
        try {
            const channelInfo = JSON.parse(providerId);
            if (!channelInfo.slug || !channelInfo.region) {
                throw new Error();
            }
            channelSlug = channelInfo.slug;
            regionFromParent = channelInfo.region;
        } catch (err) {
            throw new MLEError(
                "Could not parse 9now providerid slug or region",
                ErrorTypes.CHANNEL_MAP
            );
        }

        if (!nineNowAuthData.nuid || !nineNowAuthData.refresh_token) {
            throw new MLEError(
                "No auth data provided, exiting ",
                ErrorTypes.AUTH
            );
        }

        Store.channelSlug = channelSlug;
        Store.nuid = nineNowAuthData.nuid;
        Store.deviceId = deviceId;
        Store.aampVersion = deviceInfo.playerVersion;
        Store.devicePlatform = devicePlatform;
        if (networkConnectionType.toLowerCase() === "ethernet") {
            Store.deviceNetworkConnection = NetworkConnectionType.LAN;
        } else if (networkConnectionType.toLowerCase() === "wifi") {
            Store.deviceNetworkConnection = NetworkConnectionType.WIFI;
        }

        // All of the below have to succeed. Expect them to throw their own MLE errors on error
        await getAndStoreConfig();

        // refreshAuth depends on clientId from config
        await refreshAuth(nineNowAuthData.refresh_token, nineNowAuthData.nuid);

        // region request depends on access token from refreshAuth
        const apiRegion = await getApiRegion(Store.accessToken);
        Store.region = apiRegion || regionFromParent || "nsw";

        let { sourceUrl } = await get9NowPlaybackData(
            channelSlug,
            Store.region
        );

        try {
            // When the Yospace SDK creates a new playback URL, it'll add a session
            // ID parameter for playback analytics.
            const yospaceUrl = await LiveAAMPYospace.createSession(sourceUrl);
            sourceUrl = yospaceUrl;
        } catch {
            // If session creation fails (eg. due to timeout), then use original
            // playback URL.
        }
        // store for npaw
        Store.currentStreamUrl = sourceUrl;

        console.log("[acc] passing back video source", sourceUrl);

        return {
            aampInitConfigOverrides: {
                ...this.config?.aampInitConfigRemoteOverrides,
            },
            streams: [{ url: sourceUrl }],
        };
    }

    monitoringSetup(videoElement) {
        console.log("[acc] call monitoringSetup");
        if (!this.messengerLink) {
            this.messengerLink = new MessengerLink(Messengers);
        }
        this.videoElement = videoElement;
        this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        this.messengerLink.linkVideoElementListeners(this.videoElement);

        // expect modules to destroy themselves when appTeardown$ fires
        OztamBridge.init();
        AdobeAnalytics.init();
        AdobeAudienceManager.init();
        LiveAAMPYospace.init();
        Npaw.init();
    }

    monitoringTeardown() {
        console.log("[acc] call monitoringTeardown");
        if (this.messengerLink && this.videoElement) {
            this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        }
        Messengers.appTeardown$.send();
        Object.keys(Messengers).forEach((key) => Messengers[key].reset());
    }
}

function getFileName(url) {
    if (url) {
        return url.substring(url.lastIndexOf("/") + 1, url.lastIndexOf("."));
    }

    return "";
}
