import { Store } from "../helpers/nine-store";
import { Messengers } from "../helpers/Messengers";
import { PlayerStates } from "../helpers/utils";

const CATEGORY = "9Now-FoxtelMLE";
const COMPANY = "ninenow";

class AdobeAudienceManagerClient {
    service = null;
    configuration = {
        deviceId: Store.deviceId,
        appVersion: Store.appVersion,
        userAgent: navigator.userAgent,
    };

    channelData = null;
    videoContentStarted = false;

    listeners = [];

    init = async () => {
        if (!window.DIL) {
            console.warn("No adobe audience manager library loaded");
            return;
        }
        this.service = window.DIL.create({
            partner: "nd",
            uuidCookie: {
                name: "",
                days: 30,
            },
        });
        this.removeEventListeners(); // in case monitoringTeardown isn't called
        this.addEventListeners();
    };

    addEventListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.appTeardown$.addReceiver(this.removeEventListeners),
            Messengers.streamReady$.addReceiver(this.onFirstStreamReady, true),
            Messengers.playerState$.addReceiver(this.videoPlaybackEvent, true),
            Messengers.programChange$.addReceiver(this.onProgramChange),
        ];
    };

    removeEventListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onFirstStreamReady = (channelData) => {
        this.channelData = channelData;

        // reset session
        this.videoContentStarted = false;
    };

    onProgramChange = (channelData) => {
        this.channelData = channelData;
    };

    videoPlaybackEvent = (playerState) => {
        if (
            this.shouldSendRequest() &&
            this.channelData &&
            playerState === PlayerStates.Playing &&
            !this.videoContentStarted
        ) {
            this.setCID();

            let slug = this.channelData?.slug;
            if (slug === "channel-9") {
                slug = "ch9";
            }
            const site = "livetv";
            const hierarchy = `${CATEGORY}|livetv|${Store.region};${slug}`;
            const section = hierarchy.replace("|", ":");
            const channel = `${CATEGORY}:${site}`;

            const payload = {
                app_version: this.configuration.appVersion,
                is_user_logged_in: "1",
                user_agent: this.configuration.userAgent,
                company: COMPANY,
                hierarchy: hierarchy,
                site: site,
                section: section,
                channel: channel,
                page_name: section,
                category: CATEGORY,
                event_type: "content_video_start",
                video_title: `${CATEGORY}|live|${slug}|${Store.region}`,
                video_id: this.channelData?.listings[0].id,
                brightcove_account_id:
                    Store.appConfig?.brightcove.accountId || "",
                vms_id: `live-${slug}-syd-ssai`,
            };

            if (this.service) {
                this.service.api.signals(payload, "c_video_");
                this.service.api.submit();
            }

            this.videoContentStarted = true;
        }
    };

    shouldSendRequest = () => {
        return !!Store.nuid;
    };

    setCID = () => {
        const { deviceId } = this.configuration;

        const separator = decodeURIComponent("%01");
        const nuid = Store.nuid;

        const cid = {
            cid: `${
                encodeURIComponent("154549") +
                separator +
                encodeURIComponent(deviceId) +
                separator
            }1`,
        };

        if (nuid) {
            cid.cid_ic = `${
                encodeURIComponent("nuid") +
                separator +
                encodeURIComponent(nuid) +
                separator
            }1`;
        }

        if (this.service) {
            this.service.api.signals(cid, "d_");
        }
    };
}

export default new AdobeAudienceManagerClient();
