import {
    ErrorTypes,
    MLEError,
    setTokenInDeviceStorage,
} from "../../provider-utils";
import { Store } from "../helpers/nine-store";

export const refreshAuth = async (refreshToken, oldNuid, retryCount = 0) => {
    const maxRetries = 2;
    const refreshUrl = `${Store.apiConfig.loginUrl}/api/device/refresh-token?refresh_token=${refreshToken}&client_id=${Store.appConfig?.clientId}`;
    try {
        const refreshRequest = await fetch(refreshUrl, {
            method: "POST",
        });

        if (!refreshRequest.ok) {
            throw new MLEError(
                "Invalid refresh response.",
                ErrorTypes.PROVIDER,
                refreshRequest.status
            );
        }

        const refreshResponse = await refreshRequest.json();
        Store.accessToken = refreshResponse.access_token;

        const nuidRequest = await fetch(
            `${Store.apiConfig.nuidUrl}/?client_id=${Store.appConfig?.clientId}&token=${refreshResponse.access_token}&device_id=ctv_foxtel&nuid=${oldNuid}`
        );
        if (!nuidRequest.ok) {
            throw new MLEError(
                "Invalid refresh response.",
                ErrorTypes.PROVIDER,
                nuidRequest.status
            );
        }

        const nuidResponse = await nuidRequest.json();

        retryCount = 0;
        Store.nuid = nuidResponse.nuid;

        try {
            // do not await, we don't care about the response
            setTokenInDeviceStorage(
                "au.com.nine",
                btoa(
                    JSON.stringify({
                        nuid: Store.nuid,
                        refresh_token: refreshToken,
                    })
                )
            ).then((response) => {
                if (!response.ok || response.status !== 200) {
                    console.warn(
                        "[acc] Could not update sky auth tokens",
                        response
                    );
                }
            });
        } catch (err) {
            console.warn("[acc] Could not update sky auth tokens", err);
        }

        // Timeout expires roughly every five minutes and starts the refresh Auth process again.
        const refreshAuthExpiry = window.setTimeout(async () => {
            refreshAuth(refreshToken, Store.nuid);
            window.clearTimeout(refreshAuthExpiry);
        }, refreshResponse.expiresIn * 1000);
    } catch (err) {
        if (retryCount < maxRetries) {
            return await refreshAuth(refreshToken, oldNuid, retryCount + 1);
        } else {
            console.warn("[acc] Authentication refresh failed, throwing error");
            throw new MLEError(err.message, ErrorTypes.AUTH, err.status);
        }
    }
};
