import youbora from "youboralib";
import { Messengers } from "../helpers/Messengers";
import { PlayerStates } from "../helpers/utils";
import { Store } from "../helpers/nine-store";

export const customAAMPAdapter = youbora.Adapter.extend({
    currentBitrate: -1,
    currentTime: -1,
    listeners: [],

    /** Override to return current plugin version. */
    getVersion() {
        return `accedo-mle-${Store.appVersion}-js`;
    },

    /** Override to return current playhead of the video. */
    getPlayhead() {
        return this.currentTime;
    },

    /** Override to return current playrate. */
    getPlayrate() {
        // Fast-forward and rewind is done by seeking on an interval.
        return 1;
    },

    /** Override to return current bitrate. */
    getBitrate() {
        return this.currentBitrate;
    },

    /** Override to return rendition. */
    getRendition() {
        // API for video dimensions is unavailable on AAMP media player.
        return youbora.Util.buildRenditionString(this.getBitrate());
    },

    /** Override to return true if live and false if VOD. */
    getIsLive() {
        return this.isLive;
    },

    /** Override to return latency. */
    getLatency() {
        // API for latency is unavailable on AAMP media player.
        return 0;
    },

    /** Override to return resource URL. */
    getResource() {
        return Store.currentStreamUrl;
    },

    /** Override to return player version. */
    getPlayerVersion() {
        return Store.aampVersion;
    },

    /** Override to return player's name. */
    getPlayerName() {
        return "AAMP";
    },

    /** Register listeners to this.player. */
    registerListeners() {
        // need to bind this so we can reference the base NPAW class's methods
        this.listeners = [
            ...this.listeners,
            Messengers.streamReady$.addReceiver(this.playListener.bind(this)),
            Messengers.timeUpdate$.addReceiver(
                this.progressListener.bind(this)
            ),
            Messengers.bitrateChange$.addReceiver(
                this.onBitrateChange.bind(this)
            ),
            Messengers.playerState$.addReceiver(
                this.onPlayerStateChange.bind(this)
            ),
        ];
    },

    /** Unregister listeners to this.player. */
    unregisterListeners() {
        // Disable playhead monitoring.
        if (this.monitor) {
            this.monitor.stop();
        }

        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    },

    /** Whether the playback is for live content is set outside of the adapter. */
    setIsLive(isLive) {
        this.isLive = isLive;
    },

    onBitrateChange(bitrate) {
        this.currentBitrate = bitrate;
    },

    progressListener(time) {
        this.currentTime = time;
        if (this.getPlayhead() <= 0) {
            // hearbeat may start before player loads and plays video
            return;
        }

        if (!this.flags.isStarted) {
            if (this.startedOnce || !this.plugin.getIsLive()) {
                this.fireStart({}, "progressListener");
                this.fireJoin({}, "progressListener");
            }
        } else if (!this.flags.isJoined && this.plugin.getIsLive()) {
            this.fireJoin({}, "progressListener");
        }
    },

    /** Listener for 'play' event, we do this on init immediately */
    playListener() {
        this.fireStart({}, "playListener");
        this.startedOnce = true;
    },

    onPlayerStateChange(playerState) {
        if (playerState === PlayerStates.Playing) {
            this.playingListener();
        } else if (playerState === PlayerStates.Seeking) {
            this.seekingListener();
        } else if (playerState === PlayerStates.Paused) {
            this.pauseListener();
        } else if (playerState === PlayerStates.Buffering) {
            this.bufferingListener();
        }
    },

    /** Listener for 'pause' event. */
    pauseListener() {
        if (!this.flags.isBuffering) {
            this.firePause({}, "pauseListener");
        }
    },

    /** Listener for 'playing' event. */
    playingListener() {
        this.fireJoin({}, "playingListener");
        this.fireBufferEnd({}, "playingListener");
        this.fireSeekEnd({}, "playingListener");
        this.fireResume({}, "playingListener");
    },

    seekingListener() {
        this.fireSeekBegin({}, false, "seekingListener");
    },

    bufferingListener() {
        this.fireBufferBegin({}, false, "bufferingListener");
    },

    /** Listener for 'ended' event. Never gets called by us */
    endedListener() {
        this.fireStop({}, "endedListener");
    },
});
