import { get9NowLiveChannel } from "./nine-api";
import { Messengers } from "../helpers/Messengers";
import { Store } from "../helpers/nine-store";

let programCheckTimeout = null;
let currentChannelData = null;
const DEFAULT_PROGRAM_POLL_MS = 60 * 1000;

export const getActiveListing = (channel) => {
    const nowMs = Date.now();
    return channel?.listings?.find(
        (listing) =>
            new Date(listing.endTime).getTime() > nowMs &&
            new Date(listing.startTime).getTime() < nowMs
    );
};

const getTimeTillProgramEnd = (channel) => {
    const activeListing = getActiveListing(channel);
    if (!activeListing) {
        console.log("[acc] no matching listing found in program time check");
        return -1;
    }

    const msTillEnd = new Date(activeListing.endTime).getTime() - Date.now();
    return msTillEnd > 0 ? msTillEnd : -1;
};

const checkProgramChangedAfterMs = (timeMs) => {
    const checkTimeoutLengthMs = timeMs > 0 ? timeMs : DEFAULT_PROGRAM_POLL_MS;
    console.log(
        `[acc] Will check for program change in ${
            checkTimeoutLengthMs / 1000
        } seconds`
    );
    if (programCheckTimeout) {
        clearTimeout(programCheckTimeout);
    }
    programCheckTimeout = window.setTimeout(async () => {
        try {
            const mostRecentLiveChannelResponse = await get9NowLiveChannel(
                Store.channelSlug,
                Store.region
            );
            const activeListing = getActiveListing(
                mostRecentLiveChannelResponse
            );

            const hasProgramChanged =
                currentChannelData?.listings[0].id !== activeListing?.id;

            if (mostRecentLiveChannelResponse && hasProgramChanged) {
                currentChannelData = mostRecentLiveChannelResponse;
                Messengers.programChange$.send(currentChannelData);
                checkProgramChangedAfterMs(
                    getTimeTillProgramEnd(currentChannelData)
                );
            } else {
                checkProgramChangedAfterMs(DEFAULT_PROGRAM_POLL_MS);
            }
        } catch (err) {
            console.warn("[acc] Error polling program change", err);
            checkProgramChangedAfterMs(DEFAULT_PROGRAM_POLL_MS);
        }
    }, checkTimeoutLengthMs);
};

const onStreamReady = (channel) => {
    if (!channel) return;

    currentChannelData = channel;
    const timeTillEndMs = getTimeTillProgramEnd(channel);
    checkProgramChangedAfterMs(timeTillEndMs);
};

Messengers.streamReady$.addReceiver(onStreamReady);
Messengers.appTeardown$.addReceiver(() => {
    if (programCheckTimeout) {
        clearTimeout(programCheckTimeout);
    }
});
