import { ErrorTypes, MLEError } from "../../provider-utils";
import { Messengers } from "../helpers/Messengers";
import { Store } from "../helpers/nine-store";

const isLiveResponse = (response) => {
    return (
        response &&
        response.data &&
        response.data.getLivestream &&
        response.data.getLivestream.channels &&
        response.data.getLivestream.channels.length
    );
};

export const get9NowLiveChannel = async (channelSlug, region) => {
    const authToken = Store.accessToken;
    const nineApiUrl = `${Store.apiConfig.apiBaseUrl}/ctv/accedo/livestreams?device=ctv_foxtel&region=${region}&streamParams=foxtel&token=${authToken}`;
    const nineApiFetchResponse = await fetch(nineApiUrl).catch(() => {
        throw new MLEError(
            "HTTP error fetching 9Now livestreams API",
            ErrorTypes.PROVIDER
        );
    });

    if (!nineApiFetchResponse.ok) {
        throw new MLEError(
            "API error retrieving livestreams " +
                nineApiFetchResponse.statusText,
            ErrorTypes.PROVIDER,
            nineApiFetchResponse.status
        );
    }

    const liveResponse = await nineApiFetchResponse.json();
    if (!isLiveResponse(liveResponse)) {
        throw new MLEError(
            "Unexpected live streams API response format",
            ErrorTypes.PROVIDER
        );
    }

    const liveChannels = liveResponse.data.getLivestream.channels;

    for (let i = 0; i < liveChannels.length; i++) {
        if (liveChannels[i].slug === channelSlug) {
            return liveChannels[i];
        }
    }

    throw new MLEError(
        "Could not find channel with matching slug",
        ErrorTypes.CHANNEL_MAP
    );
};

const getChannelSourceUrl = (channel) => {
    let playbackUrl = channel.stream.url;

    if (!playbackUrl) {
        throw new MLEError(
            "No playback url for channel " + channel.slug,
            ErrorTypes.PROVIDER
        );
    }

    const deviceId = Store.deviceId;
    const nuid = Store.nuid;
    const addedQueryParamsMap = {
        deviceType: "tv",
        userAgentheader: navigator.userAgent,
        vendorVersion: Store.getOztamVendorVersion(),
        device_id: deviceId,
        oztam_session_id: Messengers.oztamSessionId$.getCurrentValue(),
        nuid: nuid || "<empty>",
        // 'yo.av' indicates to the Yospace SDK to activate analytics.
        "yo.av": "4",
        "yo.br": "true",
    };
    const queryParamsString = Object.keys(addedQueryParamsMap)
        .map((key) => `${key}=${addedQueryParamsMap[key]}`)
        .join("&");
    const querySeparator = playbackUrl?.includes("?") ? "&" : "?";

    return `${playbackUrl}${querySeparator}${queryParamsString}`;
};

export const get9NowPlaybackData = async (channelSlug, region) => {
    console.log("[acc] Channel slug is", channelSlug);

    const matchingChannel = await get9NowLiveChannel(channelSlug, region);
    if (matchingChannel) {
        const playbackUrl = getChannelSourceUrl(matchingChannel);
        Messengers.streamReady$.send(matchingChannel);
        return {
            sourceUrl: playbackUrl,
        };
    }
    throw new MLEError(
        "Could not find matching live object ",
        ErrorTypes.PROVIDER
    );
    // no try catch, handle errors in parent
};

export const getAndStoreConfig = async () => {
    await fetch(
        `${Store.apiConfig.apiBaseUrl}/accedo-support-api/v2/config/ctv_foxtel/config.json?device=ctv_foxtel`
    )
        .then((res) => res.json())
        .then((res) => (Store.appConfig = res))
        .catch(() => {
            console.log("[acc] WARNING: Invalid nine API app config request.");
        });
};

// All analytics should initialise and all api region-specific api requests
// should be done after we have a region from the nine api.
export const getApiRegion = async (
    accessToken,
    isRetryWithoutAccessToken = false
) => {
    const regionUrl = Store.apiConfig.regionServiceUrl;

    const requestUrl = isRetryWithoutAccessToken
        ? regionUrl
        : `${regionUrl}?token=${accessToken}`;

    try {
        const regionRequest = await fetch(requestUrl);
        const regionResponse = await regionRequest.json();
        return regionResponse.value;
    } catch {
        if (isRetryWithoutAccessToken) {
            console.warn("Could not get region from api -> Using fallback");
        } else {
            console.warn(
                "Could not get region with access_token -> Attempting without access_token"
            );
            return await getApiRegion(accessToken, true);
        }
    }
};
